    constructor(
        private apiService: ApiLocalService,
        private injector: Injector,
        @Optional() private favoritesTool: FavoritesTool,
        @Optional() private eventManager: EventManager,
        @Optional() private groupTool: GroupTool,
        @Optional() private cd: ChangeDetectorRef
    ) {
        this.indexId = Math.random().toString(16).substring(3);
        this.msgService = this.injector.get(MessagerService);
        this.notifyService = this.injector.get(NotifyService);
        this.inputElementId = 'sslc' + this.indexId;
        this.hostElement = this.injector.get(ElementRef);
        this.http = this.injector.get(HttpClient);
        this.cdRef = this.injector.get(ChangeDetectorRef);
        this.render = this.injector.get(Renderer2);
        this.ngZone = this.injector.get(NgZone);
        this.appRef = this.injector.get(ApplicationRef);
        
        this.loadingService = this.injector.get(LoadingService, null);
        if (this.loadingService) {
            this.dataLoadingService = new DataLoadingService();
            this.dataLoadingService.loadingService = this.loadingService;
        }

        this.localeService = this.injector.get(LocaleService, null);
        if(!this.localeService.langData['personnelSelector']) {
            this.localeService.setLocaleData(PERSONNEL_SELECTOR_LOCALE);
        }
    }

    onModelChange: any = (val?: any) => { };
    onModelTouched: any = (val?: any) => { };

    writeValue(obj: any): void {
        if (this.viewType === 'tag') {
            if (!!obj && typeof obj === 'string') {
                this.finalText = obj.split(this.multipleChoiceSeparator);
            } else {
                this.finalText = [];
            }
        } else if (this.viewType === 'text') {
            this.finalText = obj;
        }
        if(this.enableQuick){
            this.setQuickContainerWidth();
        }
    }

    registerOnChange(fn: any): void {
        this.onModelChange = fn;
    }
    registerOnTouched(fn: any): void {
        this.onModelTouched = fn;
    }
