    private dealSysUserGroupDataWhenExpandTreeNode(data: any[], path: string) {
        const pathList = [];
        if (!!path) {
            let maxLength = path.length / 4;
            if(this.enableHierarchicalLoading) {
                maxLength = maxLength - 1;
            }
            for (let a = 0; a < maxLength; a++) {
                pathList.push({ path: path.substring(0, 4 * (a + 1)) });
            }
        }else {
            pathList.push({path: ''});
        }
        const newDatas = [];
        if (data.length) {
            data.forEach(item => {
                const newsDataObj: any = {
                    data: {
                        id: item.id,
                        name: item.name,
                        path: item.path,
                        layer: Number(item.layer),
                    },
                    children: [],
                    expanded: true,
                };
                if(!!this.userGroupId && this.userGroupId !== '*') {
                    newsDataObj.selectable = item.selectable;
                }
                if(this._isInArray(item.path, 'path', pathList)){
                    newsDataObj.expanded = true;
                }else {
                    newsDataObj.expanded = false;
                    if(this.enableHierarchicalLoading && item.isDetail === false){
                        newsDataObj.leaf = false;
                    }
                }
                newDatas.push(newsDataObj);
            });
        }
        return newDatas;
    }

