    /**
     * datagrid
     */
    datagridLoad() {
        switch (this.selectTabs.activeId) {
            case this.organizationTabId:
                if (!this.stopNodeSelected) {
                    if(!this.currentSelectOrgId) {
                        return;
                    }
                    this.unSelectDatagrid.filterId = this.filterId;
                    this.unSelectDatagrid.orgId = this.currentSelectOrgId;
                    if(this.filterPosition) {
                        this.unSelectDatagrid.filterPositionId = null;
                        this.positionFilterComboList && this.positionFilterComboList.displayText && this.positionFilterComboList.onClear();
                        this.unSelectDatagrid.assembleDataByOrgIdAndGetFilterPosition({
                            pageIndex: 1,
                            pageSize: this.unSelectDatagrid.pageSize ? this.unSelectDatagrid.pageSize: this.pageSize
                        });
                    } else {
                        this.unSelectDatagrid.switchPage({
                            pageIndex: 1,
                            pageSize: this.unSelectDatagrid.pageSize ? this.unSelectDatagrid.pageSize: this.pageSize
                        });
                    }
                }
                break;
            case this.sysUserGroupTabId:
                if(!this.userGroupId) {
                    return;
                }
                this.sysUserGroupUnselect.filterId = this.filterId;
                this.sysUserGroupUnselect.userGroupId = this.userGroupId;
                if(this.showOrgFilterIcon) {
                    this.orgFilterComboList && this.orgFilterComboList.displayText && this.orgFilterComboList.onClear();
                    this.sysUserGroupUnselect.assembleDataByUserGroupIdAndGetFilterOrg({
                        pageIndex: 1,
                        pageSize: this.sysUserGroupUnselect.pageSize ? this.sysUserGroupUnselect.pageSize: this.pageSize
                    });
                } else {
                    this.sysUserGroupUnselect.switchPage({
                        pageIndex: 1,
                        pageSize: this.sysUserGroupUnselect.pageSize ? this.sysUserGroupUnselect.pageSize: this.pageSize
                    });
                }
                break;
            case this.candidateTabId:
                // if(!!this.shownOrgId && this.shownOrgId != '*' && !this.candidateDatagrid.userInShownOrg.length) {
                //     const requestParam: any = {
                //         withAllOrg: this._onlyCurrentOrg ? 0 : 1,
                //         notGetStopUser: !this.includeStopUser,
                //         notGetCurrentUser: false,
                //         orderField: this.personnelOrderField,
                //         userTypeMode: this.userTypeMode
                //     };
                //     this.apiService.getPersonnelByOrgId(this.absUserOrgUrl, this.absOrgType, this.shownOrgId, requestParam).subscribe((userData: any) => {
                //         this.candidateDatagrid.userInShownOrg = userData ? userData.data : [];
                //         this.candidateDatagrid.assembleData();
                //     });
                // } else {
                //    this.candidateDatagrid.assembleData();
                // }
                this.candidateDatagrid.assembleData();
                break;
            case this.recentTabId:
                if (!!this.shownOrgId && this.shownOrgId != '*' && !this.recentContactsComponent.userInShownOrg.length) {
                    const requestParam: PersonnelRequestParam = {
                        withAllOrg: this._onlyCurrentOrg ? 0 : 1,
                        notGetStopUser: !this.includeStopUser,
                        notGetCurrentUser: false,
                        orderField: this.personnelOrderField,
                        userTypeMode: this.userTypeMode
                    };
                    if (this.enableSecurityControl) {
                        requestParam.securityControl = true;
                        requestParam.securityLevel = this.securityLevel;
                    }
                    this.apiService.getPersonnelByOrgId(this.absUserOrgUrl, this.absOrgType, this.shownOrgId, requestParam).subscribe((userData: any) => {
                        this.recentContactsComponent.userInShownOrg = userData ? userData.data : [];
                        this.recentContactsComponent.assembleData();
                    });
                } else {
                    this.recentContactsComponent.assembleData();
                }
                break;
            case this.favoritesTabId:
                this.favoritesComponent.toggleChecked();
                break;
            case this.groupTabId:
                if(!!this.shownOrgId && this.shownOrgId != '*' && !this.groupComponent.userInShownOrg.length) {
                    const requestParam: PersonnelRequestParam = {
                        withAllOrg: this._onlyCurrentOrg ? 0 : 1,
                        notGetStopUser: !this.includeStopUser,
                        notGetCurrentUser: false,
                        orderField: this.personnelOrderField,
                        userTypeMode: this.userTypeMode
                    };
                    if (this.enableSecurityControl) {
                        requestParam.securityControl = true;
                        requestParam.securityLevel = this.securityLevel;
                    }
                    this.apiService.getPersonnelByOrgId(this.absUserOrgUrl, this.absOrgType, this.shownOrgId, requestParam).subscribe((userData: any) => {
                        this.groupComponent.userInShownOrg = userData ? userData.data : [];
                        this.groupComponent.assembleData();
                    });
                } else {
                    this.groupComponent.assembleData();
                }
                break;
        }
    }

    /**
     * 
     * @param value 
     * @param field key
     * @param array 
     * @returns boolean
     */
    _isInArray(value: string, field: string, array: any[]) {
        if (!field || !value) {
            throw new Error(this.localeService.getValue("personnelSelector.message.vaildElements"));
        }
        return array.find(item => value === item[field]) ? true : false;
    }

    /**
     * 
     * @returns null
     */
    deleteAll() {
        this.componentMap.forEach(component => {
            component.clearCheckeds();
            component.clearUserIds();
            if(component['cdRef']){
                component['cdRef']['detectChanges']();
            }
        });
        if(!!this.searchResultComponent){
            this.searchResultComponent.clearCheckeds();
        }
        this.selections = [];
        this.selectAllPersonnel = false;
        this.selectLatelyPersonnel = false;

        this.cdRef.detectChanges();
    }

    /**
     * 
     */
    moveToLeft(data: any) {
        this.lineDataChange.emit(data);
        this._removeOneFromArray(data[this.idField], this.selections);
        this.componentMap.forEach(component => {
            component.selections = _.cloneDeep(this.selections);
            component.cancelSelections({ id: data[this.idField] });
            if(component['cdRef']){
                component['cdRef']['detectChanges']();
            }
        });
        if (!!this.searchResultComponent) {
            this.searchResultComponent.cancelSelections({ id: data[this.idField] });
        }
        this.cdRef.detectChanges();
    }

    /**
     * 
     * @param originArray 
     */
    private _removeOneFromArray(activeId: string, originArray: any) {
        originArray.forEach((selection, index) => {
            if (activeId === selection[this.idField]) {
                originArray.splice(index, 1);
            }
        });
    }

