    // 
    onSearch() {
        if (!!this.searchResultComponent) {
            this.unifiedSearch({ pageIndex: 1 }, true);
            return;
        }
        this.searchPage = true;
        if (this.globalSearch) {
            this.stopNodeSelected = true;
            this.farrisTree.selectNode(this.treeListData[0].data.id);
        }
        this.assembeDataBySearch(true);
    }

    // searchResultComponent//
    private unifiedSearch(size?: any, token?: boolean) {
        if (!this.searchText) {
            return;
        }
        if (token) {
            this.searchResultComponent.pageIndex = 1;
            this.searchResultComponent.pageSize = this.pageSize;
        }
        // 
        const showTabIds = this.showTabIds.split(this.multipleChoiceSeparator);
        const pageIndex = size && size.pageIndex ? size.pageIndex : this.pageIndex;
        const pageSize = size && size.pageSize ? size.pageSize : this.pageSize;
        if (showTabIds.length === 1 && showTabIds[0] === this.candidateTabId) {
            const searchCandidateItems = this.searchUserInCandidateItems(this.searchText);
            const start = (pageIndex - 1) * pageSize;
            const end = start + pageSize;
            const result = searchCandidateItems.slice(start, end);
            this.hideSearchPanel = false;
            this.searchResultComponent.selections = this.selections;
            this.searchResult = result;
            this.searchResultComponent.itemTotalCount = searchCandidateItems.length;
            this.searchResultComponent.pageTotal = Math.ceil(searchCandidateItems.length / pageSize);
            if (this.searchResultComponent.pageTotal === 0) {
                this.searchResultComponent.pageTotal = 1;
            }
            return;
        }
        // 

        const requestParam: PersonnelRequestParam = {
            pageSize: pageSize,
            pageIndex: pageIndex,
            withAllOrg: this._onlyCurrentOrg ? 0 : 1,
            notGetStopUser: !this.includeStopUser,
            notGetCurrentUser: this._disableCurrentUser ? true : false,
            orderField: this.personnelOrderField,
            userTypeMode: this.userTypeMode,
            filterId: this.filterId
        };
        if (!!this.shownOrgId && this.shownOrgId !== '*'){
            requestParam.orgId = this.shownOrgId;
        }
        if (this.withPermission) {
            requestParam.withPermission = true;
            requestParam.authOp = 'UserManage';
        }
        if (this.enableSecurityControl) {
            requestParam.securityControl = true;
            requestParam.securityLevel = this.securityLevel;
        }
        this.apiService.getPersonnelBySearch(this.absUserOrgUrl, this.absOrgType, this.searchText,
            requestParam).subscribe((res: any) => {
                this.hideSearchPanel = false;
                this.searchResultComponent.selections = this.selections;
                this.searchResult = res ? res.data : [];
                this.searchResultComponent.itemTotalCount = res.totalCount;
                this.searchResultComponent.pageTotal = Math.ceil(res.totalCount / pageSize);
                if (this.searchResultComponent.pageTotal === 0) {
                    this.searchResultComponent.pageTotal = 1;
                }
                this.cdRef.detectChanges();
                if (this.searchResultComponent['cdRef']) {
                    this.searchResultComponent['cdRef']['detectChanges']();
                }
            });
    }

    // 
    private searchUserInCandidateItems(searchText: string) {
        const searchResult = [];
        const lowerCaseSearchText = searchText.toLowerCase();
        this.candidateItems.forEach(item => {
            if ((item.name && item.name.toLowerCase().includes(searchText)) ||
                (item.code && item.code.toLowerCase().includes(searchText))) {
                searchResult.push(item);
            } else if ((item.initials && item.initials.toLowerCase().includes(lowerCaseSearchText)) ||
                       (item.pinyin && item.pinyin.toLowerCase().includes(lowerCaseSearchText))) {
                searchResult.push(item);
            }
        });
        return searchResult;
    }

    // searchResultComponent 2022.4.6
    private assembeDataBySearch(onSearch: boolean) {
        // 
        this.componentMap.forEach(component => {
            component.searchPage = onSearch;
        });

        switch (this.selectTabs.activeId) {
            case this.organizationTabId:
                this.unSelectDatagrid.filterId = this.filterId;
                this.unSelectDatagrid.switchPage({
                    pageIndex: 1,
                    pageSize: this.pageSize
                });
                break;
            case this.sysUserGroupTabId:
                this.sysUserGroupUnselect.filterId = this.filterId;
                this.sysUserGroupUnselect.switchPage({
                    pageIndex: 1,
                    pageSize: this.pageSize
                });
                break;
            case this.candidateTabId:
                this.candidateDatagrid.assembleData();
                break;
            case this.recentTabId:
                this.recentContactsComponent.assembleData();
                break;
            case this.favoritesTabId:
                this.favoritesComponent.assembleData();
                break;
            case this.groupTabId:
                this.groupComponent.assembleData();
                break;
        }
    }

