    showQuickSelectPanel() {
        if(!this.hasSetGlobalConfigParam && this.mapFields) {
            const helpFields = Object.keys(this.mapFields);
            if(helpFields.includes(this.idField) && this.ngControl &&
               this.ngControl.formDirective &&
               this.ngControl.formDirective.form &&
               this.ngControl.formDirective.form.bindingData) {
                const bindingData = this.ngControl.formDirective.form.bindingData;
                if (bindingData.setValue) {
                    const bindingPath = this.ngControl.formDirective.form.bindingPath;
                    let pathArr: string[] = [];
                    if (bindingPath) {
                        pathArr = bindingPath.split('/').filter(n => n !== '');
                    }
                    const multiPath = this.mapFields[this.idField].split(this.multipleChoiceSeparator);
                    const fieldPaths = pathArr.concat(multiPath[0].split('.'));
                    const ids = bindingData.getValue(fieldPaths);
                    this.checkedIdsForQuickSelect = !!ids ? ids.split(',').slice() : [];
                }
            }
        } else if(!this.hasSetGlobalConfigParam && this.userIds) {
            this.checkedIdsForQuickSelect = this.userIds.split(',').slice();
        }
        this.quickPanelVisible = true;
    }

    // 
    farrisDialogshow() {
        if ((!!this.userIds && this.userIds.includes(',') && !this.multiSelect) || (this.selections.length > 1 && !this.multiSelect)) {
            this.msgService.show('warning', this.localeService.getValue("personnelSelector.message.multiSelect"), {
                height: 140,
                showHeader: false
            });
            return;
        }
        if(!this.hasSetGlobalConfigParam) {
            const getGlobalParam$ = this.apiService.getGlobalParamConfig(this.personnelParamUrl);
            const getUserPositionInfoResult$ = this.apiService.getUserPositionInfo(this.personnelParamUrl);
            getGlobalParam$.pipe(
                switchMap((res: any) => {
                    res['success'] && this.setGlobalConfigParam(res);
                    this.hasSetGlobalConfigParam = true;
                    return getUserPositionInfoResult$.pipe(
                        tap((positionIds: string[]) => {
                            if(this.withPermission) {
                                if (!positionIds || (Array.isArray(positionIds) && (positionIds.length == 0 || positionIds.toString() == 'admin'))) {
                                    this.withPermission = false;
                                    if (this.mapFields) {
                                        this.mapFields2Component();
                                    } else {
                                        if (this.isOnTop && this.showTabIds == this.candidateTabId) {
                                            this.showPersonnelSelectorDialogInWf();
                                        } else {
                                            this.showPersonnelSelectorDialog();
                                        }
                                    }
                                } else {
                                    this.getPositionAssignResult(positionIds);
                                }
                            } else {
                                if (this.mapFields) {
                                    this.mapFields2Component();
                                } else {
                                    if (this.isOnTop && this.showTabIds == this.candidateTabId) {
                                        this.showPersonnelSelectorDialogInWf();
                                    } else {
                                        this.showPersonnelSelectorDialog();
                                    }
                                }
                            }
                        })
                    )
                }),
                tap(
                    () => {
                        
                    },
                    (error) => {
                        
                    }
                )
            ).subscribe();
        } else {
            if (this.isOnTop && this.showTabIds == this.candidateTabId) {
                this.showPersonnelSelectorDialogInWf();
            } else {
                this.showPersonnelSelectorDialog();
            }
        }
    }

