import { Component, Input, EventEmitter, Output, OnInit, AfterViewInit, ChangeDetectorRef, Injector } from '@angular/core';
import * as _ from 'lodash-es';
import { noDataImg } from '../../assets/resource';
import { NotifyService } from '@farris/ui-notify';
import { FavoritesTool } from '../../utils/favorites.tool';
import { RfRequestParam, SecurityLevel } from '../../utils/user.type';

@Component({
    selector: 'lib-recent-contacts',
    templateUrl: './trc.component.html',
    styleUrls: ['./trc.component.css'],
})
export class RecentContactsComponent implements OnInit, AfterViewInit {

    // 'userId'
    @Input() idField: string;
    /**
     * 'System_organization'
     * 'Administrative_organization' 
     */
    @Input() absOrgType: string;
    // url'/api/runtime/sys/v1.0/absUserOrg'
    @Input() absUserOrgUrl: string;
    // 'common'
    @Input() busNum: string;
    // 
    @Input() searchText: any;
    // 
    @Input() searchPage: boolean;
    // false
    @Input() includeStopUser: boolean;
    // true
    @Input() includeCurrentUser: boolean;
    @Input() disableCurrentUser: boolean;
    // truetabfalse
    @Input() enableFavorites: boolean;
    // 
    @Input() validElementsInfo: string;

    @Input() multiSelect: any;
    @Input() orgId = '';
    @Input() userIds = '';
    @Input() withPermission: boolean;
    @Input() showOrgAllPathByDesc: boolean;
    @Input() enableSecurityControl: boolean;
    @Input() securityLevel: SecurityLevel;
    @Output() selectionsChange = new EventEmitter<any>();
    @Output() recentContactsChange = new EventEmitter<any>();

    private cdRef: ChangeDetectorRef;
    allChecked = false;
    columns: any;
    selections: any[] = [];
    // 
    showUserCode = true;
    cloneUserIds = '';
    cloneSelections = [];
    allIndexId = 'allIndexId';
    noDataImg = noDataImg;
    items = [];
    recentContactsList = [];
    favoritesList = [];
    userInShownOrg = [];
    private filterRecentContacts = [];

    constructor(
        private notifyService: NotifyService,
        private favoritesTool: FavoritesTool,
        private injector: Injector
    ) {
        this.allIndexId = Math.random().toString(16).substring(3, 12);
        this.cdRef = this.injector.get(ChangeDetectorRef);
    }

    ngOnInit(): void {
        this.favoritesTool.favoritesSubject.subscribe(data => {
            this.favoritesList = data;
            if (this.enableFavorites) {
                this.loadRecentContactsList();
            }
        });
    }

    ngAfterViewInit(): void {
    }

    assembleData() {
        if (this.searchPage) {
            this.searchByCodeOrName();
        } else {
            this.getRecentContactsList();
        }
    }

    searchByCodeOrName() {
        this.items = this.items.filter(item => {
            return item.code.indexOf(this.searchText) !== -1 || item.name.indexOf(this.searchText) !== -1;
        });
    }

    restoreSelections() {
        this.selections = _.cloneDeep(this.cloneSelections);
        this.toggleChecked();
        if (this.isAllChecked()) {
            this.allChecked = true;
        } else {
            this.allChecked = false;
        }
    }

    toggleAll() {
        if (!this.allChecked) {
            this.checkAll();
        } else {
            this.unCheckAll();
        }
        this.cdRef.detectChanges();
        this.emitSelections();
    }

    toggleOne(data) {
        if (data.checked) {
            this.unSelectLine(data);
        } else {
            this.selectLine(data);
        }
        this.emitSelections();
    }
    /**
     * 
     */
    loadRecentContactsList() {
        this.items = [];
        if(this.userInShownOrg.length) {
            this.filterRecentContacts = this.recentContactsList.filter(item => this.userInShownOrg.find(user => user.userId == item.userId));
        } else {
            this.filterRecentContacts = this.recentContactsList;
        }
        this.filterRecentContacts.forEach(res => {
            const item = _.cloneDeep(res);
            item.checked = false;
            item.inFavorites = false;
            item.indexId = Math.random().toString(16).substring(2);
            if (this._isInArray(res[this.idField], this.idField, this.selections) !== -1) {
                item.checked = true;
            }
            if (this._isInArray(res[this.idField], this.idField, this.favoritesList) !== -1) {
                item.inFavorites = true;
            }
            item.displayName = !!this.showUserCode ? ('[' + item.code + ']' + item.name) : item.name;
            if (this.showOrgAllPathByDesc && item.orgAllPath) {
                item.orgAllPath = this.changeDisplayOrgAllPath(item.orgAllPath);
            }
            this.items.push(item);
        });
        this.toggleChecked();
        this.cdRef.detectChanges();
    }

    /**
     * 
     * @param dataArr 
     */
    checkAll() {
        this.items.forEach(item => {
            this._insertOneIntoSelections(item);
            item.checked = true;
        });
    }

    /**
     * 
     * @param dataArr 
     * @returns void
     */
    unCheckAll() {
        this.items.forEach(item => {
            this.selections.forEach((selection, index) => {
                if (item[this.idField] === selection[this.idField]) {
                    this.selections.splice(index, 1);
                }
            });
            item.checked = false;
        });
    }

    /**
     * 
     * @param data 
     */
    selectLine(data: any) {
        if (this.multiSelect) {
            this._insertOneIntoSelections(data);
        } else {
            this.userIds = '';
            this.selections = [];
            this.items.forEach(item => {
                if (item.checked) {
                    item.checked = false;
                }
            });
            this._insertOneIntoSelections(data);
        }
        if (this.isAllChecked()) {
            this.allChecked = true;
        }
    }

    /**
     * 
     * @param data 
     */
    unSelectLine(data: any) {
        const index = this._isInArray(data[this.idField], this.idField, this.selections);
        if (index > -1) {
            this.selections.splice(index, 1);
        }
        if (this.allChecked) {
            this.allChecked = false;
        }
    }

    private emitSelections() {
        const emitObj = {
            selections: this.selections,
            token: 'recent'
        };
        this.selectionsChange.emit(emitObj);
    }

    /**
     * 
     */
    getRecentContactsList() {
        const param: RfRequestParam = {
            absOrgType: this.absOrgType,
            bizId: this.busNum,
            objectValue: 'RecentContacts'
        };
        if (this.disableCurrentUser) {
            const currentUserId = (window as any).gspframeworkService.common.userInfos.get().id;
            param.filterUserIds = [currentUserId];
        }
        if (this.withPermission) {
            param.withPermission = true;
            param.authOp = 'UserManage';
        }
        if (this.enableSecurityControl) {
            param.securityControl = true;
            param.securityLevel = this.securityLevel;
        }
        this.favoritesTool.curdRecentContactsOrFavorites('query', param).subscribe((rcRes: any) => {
            const rcResData = rcRes ? rcRes.data : [];
            this.recentContactsList = rcResData;
            this.loadRecentContactsList();
            const ids = [];
            this.recentContactsList.forEach(item => {
                ids.push(item[this.idField]);
            });
            this.recentContactsChange.emit({
                recentContactsList: ids
            });
        });
    }

    toggleFavorites(e: any, item: any) {
        this.favoritesTool.toggleFavorites(e, item);
    }

    /**
     * 
     */
    clearRecentContactsList() {
        if (!this.items.length) {
            return;
        }
        const param: RfRequestParam = {
            absOrgType: this.absOrgType,
            bizId: this.busNum,
            objectValue: 'RecentContacts',
            deleteList: this.items.map(item => item[this.idField]),
            clearData: true
        };

        this.favoritesTool.curdRecentContactsOrFavorites('modify', param).subscribe((val) => {
            this.items = [];
            this.cdRef.detectChanges();
            this.recentContactsChange.emit({
                recentContactsList: []
            });
            // this.notifyService.info('');
        });

    }

    /**
     *  
     * @param $event 
     */
    cancelSelections($event) {
        if (this.items.findIndex(item => item[this.idField] === $event.id) === -1) {
            return;
        }
        this.items.forEach(item => {
            if (item[this.idField] === $event.id) {
                item.checked = false;
            }
        });
        this.selections.forEach((selection, index) => {
            if ($event.id === selection[this.idField]) {
                this.selections.splice(index, 1);
            }
        });
        this.allChecked = false;
    }

    /**
     * 
     */
    clearCheckeds() {
        this.items.forEach(item => {
            item.checked = false;
        });
        this.allChecked = false;
        this.selections = [];
    }

    clearUserIds() {
        this.userIds = '';
    }
    /**
     * 
     * @param data 
     * @param collection 
     */
    _insertOneIntoSelections(data: any) {
        // 
        if (this._isInArray(data[this.idField], this.idField, this.selections) > -1) {
            return;
        }
        this.selections.push(data);
        this.selections = this.selections.slice();
    }

    /**
     * 
     * @param value value
     * @param field field
     * @param array array
     * @returns 
     */
    _isInArray(value: string, field: string, array: any[]): number {
        if (!field || !value) {
            throw new Error(this.validElementsInfo);
        }
        return array.findIndex(item => value === item[field]);
    }

    /**
     * 
     */
    toggleChecked() {
        this.items.forEach(item => {
            if (this._isInArray(item[this.idField], this.idField, this.selections) > -1) {
                item.checked = true;
            } else {
                item.checked = false;
            }
        });
        if (this.isAllChecked()) {
            this.allChecked = true;
        } else {
            this.allChecked = false;
        }
    }

    private isAllChecked(): boolean {
        let count = 0;
        let allChecked = false;

        this.items.forEach(item => {
            this.selections.forEach(selection => {
                if (item[this.idField] === selection[this.idField]) {
                    count += 1;
                }
            });
        });
        if (this.items.length === count && count > 0) {
            allChecked = true;
        }
        return allChecked;
    }

    private changeDisplayOrgAllPath(data: string) {
        if(!data) {
            return '';
        }
        const orgAllPathArray = data.split('_');
        return orgAllPathArray.reverse().join('_');
    }

    trackByFn(index, item) {
        return index;
    }

}
