import {
    Component, Input, EventEmitter, Output, OnInit, AfterViewInit, ChangeDetectorRef, Optional
} from '@angular/core';
import * as _ from 'lodash-es';
import { noDataImg } from '../../assets/resource';
import { NotifyService } from '@farris/ui-notify';
import { FavoritesTool } from '../../utils/favorites.tool';
import { PanelMode, RfRequestParam, SecurityLevel } from '../../utils/user.type';
import { ApiLocalService } from '../../utils/api-service';
import { debounceTime, distinctUntilChanged } from 'rxjs/operators';
import { BehaviorSubject } from 'rxjs';
import { trim } from 'lodash-es';
import { BasePathService } from '@farris/rtf';

@Component({
    selector: 'lib-quick-select',
    templateUrl: './qsp.component.html',
    styleUrls: ['./qsp.component.css'],
})
export class QuickSelectComponent implements OnInit, AfterViewInit {

    @Input() idField: string;
    @Input() multiSelect: boolean;
    @Input() enableChildHierarchy: boolean;
    @Input() includeStopUser: boolean;
    @Input() personnelOrderField: string;
    @Input() includeCurrentUser: boolean;
    @Input() disableCurrentUser: boolean;
    @Input() userTypeMode: string;
    @Input() filterId: string;
    @Input() absUserOrgUrl: string;
    @Input() set quickInput(value: any) {
        if (!this.firstLoad) {
            this.searchText = trim(value);
            this.searchTextStream.next(value);
        } else if (!this.hasSetGlobalConfigParam) {
            const personnelSelectorParamUrl = '/api/runtime/sys/v1.0/personnel-selector-param';
            const basePathUrl = BasePathService.convertPath(personnelSelectorParamUrl);
            this.apiService.getGlobalParamConfig(basePathUrl).subscribe((res: any) => {
                res['success'] && this.setGlobalConfigParam(res);
                this.hasSetGlobalConfigParam = true;
            });
        }
        this.firstLoad = false;
        this.checkeds = [];
    };
    @Input() enableFavorites: boolean;
    @Input() absOrgType: string;
    @Input() busNum: string;
    @Input()
    set checkedDataIds(value: string[]) {
        this._checkedDataIds = value;
    }
    get checkedDataIds() {
        return this._checkedDataIds;
    }
    @Input() set originalSelections(value: any[]) {
        this.selections = value.slice();
    };
    @Input() validElementsInfo: string;
    @Input() hasSetGlobalConfigParam: boolean = false;
    @Input() withPermission: boolean;
    @Input() showOrgAllPathByDesc: boolean;
    @Input() enableSecurityControl: boolean;
    @Input() securityLevel: SecurityLevel;
    @Output() selectionsChange = new EventEmitter<any>();
    @Output() togglePanelModelChange = new EventEmitter<PanelMode>();

    allChecked = false;
    allIndexId = 'allIndexId';
    noDataImg = noDataImg;
    items = [];
    recentContactsList = [];
    favoritesList = [];
    searchText = '';
    private searchTextStream: BehaviorSubject<string> = new BehaviorSubject<string>('');
    selections = []
    firstLoad = true;
    checkeds = [];
    // 
    _checkedDataIds = [];
    // 
    showUserCode = true;

    constructor(
        @Optional() private apiService: ApiLocalService,
        @Optional() private notifyService: NotifyService,
        @Optional() private favoritesTool: FavoritesTool,
        @Optional() private cdRef: ChangeDetectorRef

    ) {
        this.allIndexId = Math.random().toString(16).substring(3, 12);
    }

    ngOnInit(): void {
        // 
        if (this.favoritesTool) {
            this.favoritesTool.getFavoritesList().subscribe((result) => {
                this.favoritesList = result ? (result as any).data : [];
                this.favoritesTool.favoritesList = this.favoritesList.slice();
                this.getRecentContactsList();
            });
            this.favoritesTool.favoritesSubject.subscribe(data => {
                this.favoritesList = data;
                this.assembleItemsFavorites();
            })
        }

        this.searchTextStream.pipe(
            debounceTime(500)
        ).subscribe(data => {
            if (!!data) {
                this.getItemsBySearch(data);
            } else {
                this.items = this.recentContactsList.slice();
                this.assembleItemsChecked();
                this.assembleItemsFavorites();
            }
        });
    }

    ngAfterViewInit(): void {

    }

    private setGlobalConfigParam(param: any) {
        if(this.userTypeMode == 'WfMode' && param.hasOwnProperty('showUserCode') && this.showUserCode !== param['showUserCode']) {
            this.showUserCode = param['showUserCode'];
        }
        // 
        if(param.hasOwnProperty('showOrgAllPathByDesc') && this.showOrgAllPathByDesc !== param['showOrgAllPathByDesc']) {
            this.showOrgAllPathByDesc = param['showOrgAllPathByDesc'];
        }
        // 
        if(this.withPermission && param.hasOwnProperty('withPermission') && this.withPermission !== param['withPermission']) {
            this.withPermission = param['withPermission'];
        }
    }

    private getItemsBySearch(searchText: any) {
        const searchItems = this.recentContactsList.slice();
        if (!searchText) {
            return;
        }
        this.items = searchItems.filter(item => (item.code && item.code.indexOf(searchText) !== -1) || (item.name && item.name.indexOf(searchText) !== -1) || (item.initials && item.initials.indexOf(searchText) !== -1) || (item.pinyin && item.pinyin.indexOf(searchText) !== -1));
        this.assembleItemsChecked();
    }

    private getRecentContactsList() {
        const param: RfRequestParam = {
            absOrgType: this.absOrgType,
            bizId: this.busNum,
            objectValue: 'RecentContacts'
        };
        if (this.disableCurrentUser) {
            const currentUserId = (window as any).gspframeworkService.common.userInfos.get().id;
            param.filterUserIds = [currentUserId];
        }
        if (this.withPermission) {
            param.withPermission = true;
            param.authOp = 'UserManage';
        }
        if (this.enableSecurityControl) {
            param.securityControl = true;
            param.securityLevel = this.securityLevel;
        }
        this.favoritesTool.curdRecentContactsOrFavorites('query', param).subscribe((rcRes: any) => {
            const rcResData = rcRes ? rcRes.data : [];
            rcResData.forEach(res => {
                res.displayName = !!this.showUserCode ? ('[' + res.code + ']' + res.name) : res.name;
                if(this.showOrgAllPathByDesc && res.orgAllPath) {
                    res.orgAllPath = this.changeDisplayOrgAllPath(res.orgAllPath);
                }

            });
            this.recentContactsList = rcResData.slice();
            this.items = rcResData.slice();
            this.assembleItemsChecked();
            if (this.enableFavorites) {
                this.assembleItemsFavorites();
            }
        });
    }

    togglePanelMode(showQuickMode: boolean, showAdvancedMode: boolean) {
        this.togglePanelModelChange.emit({
            showQuickMode,
            showAdvancedMode
        });
    }

    toggleAll() {
        if (!this.allChecked) {
            this.checkAll();
        } else {
            this.unCheckAll();
        }
    }

    toggleOne(data) {
        if (data.checked) {
            this.unSelectLine(data);
        } else {
            this.selectLine(data);
        }
    }

    assembleItemsChecked(): void {
        this.items.forEach(item => {
            item.checked = false;
            item.indexId = Math.random().toString(16).substring(2);
            if ((this.selections.length && this._isInArray(item[this.idField], this.idField, this.selections) !== -1)
                || (this.checkedDataIds.includes(item[this.idField]))) {
                item.checked = true;
                if (this._isInArray(item[this.idField], this.idField, this.checkeds) === -1) {
                    this.checkeds.push(item);
                }
            }
        });
        this.items = this.items.slice();
        this.checkeds = this.checkeds.slice();
        this.toggleAllChecked();
    }

    assembleItemsFavorites() {
        this.items.forEach(item => {
            item.inFavorites = false;
            if (this._isInArray(item[this.idField], this.idField, this.favoritesList) !== -1) {
                item.inFavorites = true;
            }
        });
        this.items = this.items.slice();
    }

    /**
     * 
     * @param dataArr 
     */
    checkAll() {
        this.items.forEach(item => {
            this._insertOneIntoSelections(item);
            item.checked = true;
        });
    }

    /**
     * 
     * @param dataArr 
     * @returns void
     */
    unCheckAll() {
        const uncheckedIds = [];
        this.items.forEach(item => {
            this.selections.forEach((selection, index) => {
                if (item[this.idField] === selection[this.idField]) {
                    uncheckedIds.push(item[this.idField]);
                }
            });
            item.checked = false;
        });
        this.selections = this.selections.filter(selection => !uncheckedIds.includes(selection[this.idField]));
        this.checkeds = this.checkeds.filter(item => !uncheckedIds.includes(item[this.idField]));
    }

    /**
     * 
     * @param data 
     */
    selectLine(data: any) {
        if (this.multiSelect) {
            this._insertOneIntoSelections(data);
        } else {
            this.selections = [];
            this.items.forEach(item => {
                if (item.checked) {
                    item.checked = false;
                }
            });
            this._insertOneIntoSelections(data);
        }
        this.toggleAllChecked();
    }

    /**
     * 
     * @param data 
     */
    unSelectLine(data: any) {
        const index = this._isInArray(data[this.idField], this.idField, this.selections);
        if (index > -1) {
            this.selections.splice(index, 1);
        }
        const indexc = this._isInArray(data[this.idField], this.idField, this.checkeds);
        if (indexc > -1) {
            this.checkeds.splice(indexc, 1);
        }
        if (this.allChecked) {
            this.allChecked = false;
        }
    }

    // 
    confirmSelections(): void {
        const recentContactsList = [];
        this.recentContactsList.forEach(item => {
            recentContactsList.push(item[this.idField]);
        });
        const emitObj = {
            selections: this.selections,
            token: 'quick',
            recentContactsList
        };
        this.selectionsChange.emit(emitObj);
    }

    toggleFavorites(e: any, item: any) {
        this.favoritesTool.toggleFavorites(e, item);
    }

    /**
     * 
     * @param data 
     * @param collection 
     */
    _insertOneIntoSelections(data: any) {
        // 
        if (this._isInArray(data[this.idField], this.idField, this.selections) > -1) {
            return;
        }
        this.selections.push(data);
        this.checkeds.push(data);
        this.selections = this.selections.slice();
        this.checkeds = this.checkeds.slice();
    }

    /**
     * 
     * @param value value
     * @param field field
     * @param array array
     * @returns 
     */
    _isInArray(value: string, field: string, array: any[]): number {
        if (!field || !value) {
            throw new Error(this.validElementsInfo);
        }
        return array.findIndex(item => value === item[field]);
    }

    /**
     * 
     */
    toggleAllChecked() {
        if (this.items.length === this.checkeds.length && this.checkeds.length > 0) {
            this.allChecked = true;
        } else {
            this.allChecked = false;
        }
    }

    private changeDisplayOrgAllPath(data: string) {
        if(!data) {
            return '';
        }
        const orgAllPathArray = data.split('_');
        return orgAllPathArray.reverse().join('_');
    }

    trackByFn(index, item) {
        return index;
    }

}
